.header {
  width: 100%;
  background-color: #ffffff;
  height: 80px;
  position: fixed;
  font-family: $pointsFont;
  z-index: 10;
}
.header .container {

}
.header__content {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.logo a {
  font-size: 2rem;
  color: red;
}
.header__menu {
  
}
.header__menu-list {
  display: flex;
  gap: 6rem;
  font-size: 1.4rem;
  line-height: 1.6rem;
  cursor: pointer;
}
.header__menu-item {
  font-size: 2rem;
}
.header__menu-link {
}

.header_social {

}
.header__social-list {
  display: flex;
  gap: 3rem;
  
}
.header__social-item {
  padding: 2rem 0;
}
.header__social-item img {
  width: 4rem;
  display: block;
}



