/* Общие стили элементов */

/* Кнопки */
.button {
  font-size: 2.2rem;
  background-color: #C70405;
  border: none;
  font-family: $titleFont;
  color: #ffffff;
  font-weight: 600;
  padding: 2rem 8rem;
  border-radius: 1.4rem;
  -webkit-box-shadow: 0px 2px 17px -6px rgba(0,0,0,0.75);
  -moz-box-shadow: 0px 2px 17px -6px rgba(0,0,0,0.75);
  box-shadow: 0px 5px 12px -3px rgba(0,0,0,0.75);
  cursor: pointer;
  transition: opacity ease-in 0.6s;
}
.button:hover {
  opacity: 0.9;
  transition: opacity ease-in 0.6s;
}

/* Заголовки */
.title {
  color: #C70405;
  font-weight: 600;
  position: relative;
  display: inline-block;
  font-family: $titleFont;
  font-size: 4rem;
  margin-bottom: 4rem;
}
.subtitle {
  font-size: 2.6rem;
}