*, ::before, ::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
html {
  line-height: 1;
  -ms-text-size-adjust: 100%;
	-moz-text-size-adjust: 100%;
	-webkit-text-size-adjust: 100%;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
  font-size: 62.5%;
}
body, html {
  height: 100%;
  min-width: 32rem;
  color: #000000;
}
main {
  display: block;
}
a {
  text-decoration: none;
  background-color: transparent;
  color: #000000;
}
h1, h2, h3, h4, h5, h6, p {
  margin: 0;
}
ul, ol, li {
  list-style-type: none;
  margin: 0;
  padding: 0;
}
hr {
  box-sizing: content-box;
  height: 0;
  overflow: visible;
}
img {
  border-style: none;
}
button,
input,
optgroup,
select,
textarea {
  font-family: inherit; 
  font-size: 100%; 
  line-height: 1.15; 
  margin: 0; 
}
button,
select {
  text-transform: none;
}
button,
[type="button"],
[type="reset"],
[type="submit"] {
  -webkit-appearance: button;
}
