@media screen and (max-width: 991px) {
	/* header */
	.header__menu-list {
		gap: 4rem;
	}
	.header__menu-link {
		font-size: 1.6rem;
	
	}
	.header__social-list {
		gap: 2.5rem;
	}
}
@media screen and (max-width: 850px) {
	.header__menu-list {
		gap: 3rem;
	}
}
@media screen and (max-width: 767px) {
	/* header */
	.header {
		height: 7rem;
	}
	.header__menu {
		position: fixed;
		left: 100%;
		top: 7rem;
		padding: 4rem 0;
		background-color: #ffffff;
		width: 100%;
		height: 100%;
		transition: 0.5s;
	}
	.header__menu.active {
		left: 0;
		transition: 0.5s;
	}
	.header__menu-list {
		flex-direction: column;
		align-items: flex-end;
		padding: 1.5rem 2.7rem 1.5rem 2.7rem;
		gap: 4rem;
	}
	.header__menu-link {
		font-size: 2.4rem;
	}
	.header__social-item {
		padding: 1.5rem 0;
	}
	/* intro */
	.intro__inner {
		margin-top: 3rem;
	}

}
@media screen and (max-width: 640px) {
	/* header */
	.header__social-item {
		padding: 1rem 0;
	}
	.header__social-item img {
		width: 3rem;
	}
	.header {
		height: 5.5rem;
	}
	.header__menu {
		top: 5.5rem;
	}
		/* intro */
		.intro__inner {
			margin-top: 1.5rem;
		}
}

@media screen and (max-width: 480px) {
	.header__social-list {

	}
}