}
.container {
}
.title::after {
  content: "";
  position: absolute;
  top: 110%;
  left: 25%;
  height: 0.5rem;
  width: 75%;
  background-color: #000000;
}
.info__title {
}
.info__title-item {
}
.info__inner {
}
.info__description {
  font-size: 4rem;
  line-height: 1.4;
  margin: 4rem 0;
}
.info__subtitle-wrapper {
  display: flex;
  justify-content: center;
  margin-bottom: 6rem;
}

.info__subtitle {
  line-height: 1.4;
  font-size: 5rem;
  position: relative;
  display: inline-block;
}
.info__subtitle::after {
  content: "";
  position: absolute;
  top: 110%;
  left: 25%;
  height: 0.5rem;
  width: 75%;
  background-color: #000000;
}
.info__list {
  font-size: 2.6rem;
  line-height: 1.4;
  display: flex;
  flex-direction: column;
  gap: 4rem;
}
.info__list-item {
  min-height: 7.3rem;
  background: url('../images/dot.png') no-repeat;
  background-position: left top;
  background-size: 7rem 5rem;
  padding-left: 10rem; /* отступ подбираем исходя из размера картинки */
}

.info__list-item--important {
  font-weight: 600;
  color: #C70405;
}