/* Подключение шрифтов */
@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;700&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Raleway:wght@300;400;600&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Playfair+Display:wght@400;600&display=swap');
// Локальный шрифт
//@use 'fonts';

/* Шрифт по умолчанию */
$mainFont: 'Roboto', sans-serif;
$pointsFont: 'Raleway', sans-serif;
$titleFont: 'Playfair Display', serif;
$fontSize: 16rem;
$mainFontWeight: 400;

/* Основные цвета */
$mainColor: #000; // Цвет шрифта по умолчанию

$backgroundMain: #96bdf7; // Цвет фона body

$backgroundDark: #b7fdff; // Цвет фона блоков
$backgroundLight: #c6fae2;

/* Ограничивающий контейнер */
$maxWidth: 140rem; // Ширина контейнера

/* Подключение обнуления */
@import './block/base';

body {
  background-color: $backgroundMain;
  color: $mainColor;
  font-family: $mainFont;
}

img {
  width: 100%;
  max-width: 100%;
  object-fit: cover;
  display: block;
}

/* Стили оболочки */
.wrapper {
  position: relative;
  margin: 0 auto;
  min-height: 100%;
  display: flex;
  flex-direction: column;
  overflow: hidden;
  // Прижимаю футер
  > main {
    flex: 1 1 auto;
  }
}
.container {
  outline: 1px solid red;
  max-width: $maxWidth;
  width: 100%;
  margin: 0 auto;
  padding: 0 2rem;
}

/* Подключение стилей общих элементов проекта */
@import './common.scss';

/* Подключение отдельных блоков проекта */
@import './header';
@import './main';
@import './intro';
@import './info';
@import './footer';

/* Адаптив */
@import './media';

/* Подключение отдельных элементов */
@import './block/_burger';


